body {
  font-family: "Baloo Tammudu 2", cursive;
}

.contain {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(375px, 1fr));
  grid-gap: 0px;
  width: 90%;
  margin: auto;
  margin-top: 3rem;
}

.nav-bar {
  position: sticky;
  width: 60%;
  margin: auto;
  margin-top: 1rem;
  background-color: #f4b41a;
  height: 5rem;
  justify-content: center;
  text-align: center;
  border-radius: 10px;
}

.nav-bar-text {
  position: relative;
  width: fit-content;
  margin: auto;
  top: 1.6rem;
  font-size: xx-large;
  font-weight: 900;
  font-family: "Baloo Tammudu 2", cursive;
  color: #142d59;
}

.card {
  display: grid;
  border: 3px solid black;
  width: 100%;
  grid-template-rows: 1fr 3fr 1fr;
  background-color: #dbf3fa;
}

.date {
  margin-top: 10px;
  width: 70%;
  margin: 5px auto;
}
.date-d {
  background-color: whitesmoke;
  border: 2px solid black;
  border-radius: 10px;
  width: fit-content;
  color: black;
  margin: auto;
  padding-top: 0.7rem;
}

.teams {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}

.team-text {
  font-size: xx-large;
  padding: 7px;
}

.vs {
  font-size: x-large;
  font: bolder;
}

.result-button {
  width: 40%;
  margin: auto;
}

@media screen and (max-width: 403px) {
  .contain {
    margin-left: 0px;
  }
}
